.cover {
  position: relative;
  top: 0;
  left: 0;
  width: 30%;
  height: 100%;
  z-index: 100;

  @include breakpoint(medium) {
    width: 100%;
    height: 30%;
    min-height: 300px;
  }
}

.cover-image {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  box-shadow: inset -1px 0 0 rgba(0, 0, 0, 0.3);
  background-color: $drawer-background-color;
}

.cover-container {
  position: absolute;
  bottom: 0;
  padding: 50px 6% 6%;
  width: 100%;
  color: #fff;
  text-shadow: 0 1px 3px rgba(0, 0, 0, 0.3);
  text-align:center;

  header{
    .cover-title {
      font-size:51px;
      font-weight: 200;
      margin: 6px 0;
    }
    .cover-description{
      color:#ccc;
      b{
        font-weight:200;
        color:#fff;
      }
    }
  }

  @include breakpoint(medium) {
    text-align: center;
    padding: 0 15px;
  }
}

.cover-logo {

  display:inline-block;
  margin-right:20px;

  img {
    border: 3px solid #fff;
    border-radius: 50%;
    border-radius: 50%;
    max-height: 100px;
    max-width: 100px;
    transition: opacity .3s ease-in-out;
    box-shadow:
      0 0 3px 3px rgba(0, 0, 0, 0.3),
      inset 0 0 40px 2px rgba(0, 0, 0, 0.3);
    backface-visibility: hidden;
  }

  h1{

  }

  &:hover,
  &:focus {
    img {
      opacity: .85;
    }
  }
}

.cover-title {
  font-size: 32px;
  line-height: 1;
  margin-bottom: 12px;
}

.cover-description {
  font-size: 15px;
  line-height: 1.2;
}
